import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import { Pane, Heading, Button, AddIcon, EditIcon, SideSheet, Paragraph } from 'evergreen-ui'
import Code from '../../@components/Code'
import ConfigService from '../../@services/Config';

class PageConfigOverview extends Component {
  constructor(props)
  {
    super(props)
    this.state = {
      menuStruct: props?.menuStruct,
      isShown: false,
      configString: null
    }
  }
  render() {
    return (
      <article>
        <Pane display="flex" padding=".5em" background="tint2">
          <Pane flex={1} alignItems="center" display="flex">
            <Heading size={600}>Config Overview</Heading>
          </Pane>
          <Pane>
            <NavLink to={`/${this.props.match.params.siteKey}/templates`}><Button appearance="minimal" iconBefore={AddIcon}>Add Template</Button></NavLink>
            <Button appearance="minimal" iconBefore={EditIcon} onClick={(e) => { e.preventDefault(); this.setState({ isShown: true, configString: JSON.stringify(ConfigService.site['@config'], true, 4) }) }}>View Code</Button>
          </Pane>
        </Pane>
        <Pane display="flex" flexWrap="wrap">
          {
            this.state.menuStruct.map((item, index) => (
              <Pane className="griditem__block" key={index} flex="0 0 calc(25% - 2em)" borderRadius={5} height={128} display="flex" justifyContent="center" alignItems="center" margin="1em" elevation={1} border="default">
                <NavLink to={`/${this.props.match.params.siteKey}/config/${item.key}`}>
                  <Pane flexDirection="column" display="flex" justifyContent="center" alignItems="center">
                    <div className="navlink__icon">{item.icon}</div>
                    <Heading size={500}>
                      {item.label}
                    </Heading>
                  </Pane>
                </NavLink>
              </Pane>
            ))
          }
        </Pane>
        <Pane padding="1em">
          <Heading size={400}>What is this?</Heading>
          <Paragraph size={400} color="muted">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam laoreet non augue et faucibus. Ut arcu tellus, hendrerit id sem in, iaculis varius ipsum. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Etiam ac aliquet orci. Donec porta risus at nisi placerat pellentesque. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia curae; Fusce interdum ligula ut cursus egestas. Praesent porttitor non nisl quis sodales. Maecenas efficitur, nibh ac finibus vestibulum, arcu ex tempus nibh, eu convallis lacus diam id elit. Pellentesque elementum ac nibh mattis molestie. Quisque urna turpis, viverra vel tortor sit amet, tincidunt condimentum arcu. Pellentesque luctus aliquam consequat. Vivamus condimentum non risus eget venenatis.
          </Paragraph>
        </Pane>
        <SideSheet
          isShown={this.state.isShown}
          onCloseComplete={() => this.setState({ isShown: false })}
        >
          <Code codeString={this.state.configString} />
        </SideSheet>
        <SideSheet
          isShown={this.state.generateShown}
          onCloseComplete={() => this.setState({ generateShown: false })}
        >
          <h1>GENERATE_CODE</h1>
        </SideSheet>
      </article>
    )
  }
}

export default PageConfigOverview;